@keyframes foldDown {
  0% {
    transform: rotateX(-90deg);
    transform-origin: top;
    opacity: 0;
  }
  100% {
    transform: rotateX(0deg);
    transform-origin: top;
    opacity: 1;
  }
}

.navbar {
  background: linear-gradient(90deg,#0a192f, #0a192f);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  font-family: 'Times New Roman', Times, serif;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
  
}

.navbar-logo {
  color: #0ef;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  font-family: 'Times New Roman', Times, serif;
  
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #0ef;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-family: 'Times New Roman', Times, serif;
}

.nav-links::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 4px;
  background: linear-gradient(90deg, rgba(100,255,218,1) 0%, rgba(146,254,157,1) 100%); /* A gradient for the underline */
  transition: width 0.4s ease-in-out; /* Transition for the sliding effect */
}

.nav-links:hover::after {
  width: 100%; /* On hover, the pseudo-element (underline) will fill the width */
}

.nav-links:hover {
  color: #64ffda; /* Bright cyan for text color on hover */
}

.fa-bars {
  color: #0ef;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

/* Apply the foldDown effect to each navbar link */
.nav-links {
  animation: foldDown 0.6s ease forwards;
  animation-delay: calc(0.1s * var(--i));
}

/* You will need to set a custom property for each link to control the delay */
