/* App.css */

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 40px, 0);
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.resume-container,
.resume-footer,
.resume-header {
  animation-name: fadeInUp;
  animation-duration: 3s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}

:root {
  --text-primary: #E6E6E6; /* Light grey for primary text */
  --bg-primary: #0a192f; /* Very dark grey for background */
  --accent-color: #0db8de; /* Bright blue for accents */
  --header-font: 'Orbitron', sans-serif; /* Futuristic font */
  --body-font: 'Roboto', sans-serif; /* Simple, readable font */
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--body-font);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
}

h2 {
  margin-top: 0px;
  margin-bottom: 20px;
  font-family: 'Pirata One', sans-serif;
  font-size: 45px;
  line-height: 80px;
  font-weight: 400;
  color: var(--accent-color);
}

h3 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-family: Poppins, sans-serif;
  font-size: 24px;
  line-height: 1.3;
  font-weight: 400;
  box-shadow: 0 0 5px #0a192f, 0 0 15px rgb(13, 45, 104);
}

.resume-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 20px;
  /* box-shadow: 0 0 5px #0ef, 0 0 15px #0ef; */
}

.resume-header {
  text-align: center;
  margin-bottom: 40px;
}

.resume-header h1 {
  font-family: var(--header-font);
  font-size: 3.5rem;
  color: var(--accent-color);
  
}

.resume-header a {
  color: var(--accent-color);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
  
}

.resume-header a:hover,
.resume-header a:focus {
  color: #0ef;
  text-decoration: none;
  box-shadow: 0 0 5px #0ef, 0 0 15px #0ef;
}

.section {
  margin-bottom: 30px;
}

.section h2 {
  font-family: var(--header-font);
  font-size: 1.5rem;
  color: var(--accent-color);
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px solid var(--accent-color);
}

.section p,
.section a {
  font-size: 1rem;
  margin: 10px 0;
}

.section a {
  text-decoration: underline;
  color: var(--accent-color);
}

.section a:hover {
  color: #ffffff;
}

.resume-footer {
  text-align: center;
  padding: 20px 0;
  font-size: 0.9rem;
}

.section-4 {
  margin-top: 10vh;
  margin-bottom: 10vh;
}


.container-8 {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
  grid-auto-columns: 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  -ms-grid-rows: auto auto;
  grid-template-rows: auto auto;
}


.link.dark {
  position: relative;
  z-index: 2;
  display: inline-block;
  width: 100%;
  margin: 16px auto;
  padding-top: 8px;
  padding-bottom: 8px;
  border-width: 4px;
  border-radius: 2px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  
}

.link.dark:hover {
  background-color: #0c3273;
  color: #0ef;
  box-shadow: 0 0 5px #0c3273, 0 0 15px #0c3273, 0 0 10px #0c3273, 0 0 50px #0c3273,
    0 0 50px #0c3273;
}

.link.dark:active {
  margin-top: 2px;
  margin-right: -2px;
  background-color: #0ef;
  color: #f2f7fc;
}

.link.dark.b2t {
  width: 140px;
  margin-right: 10px;
}

.link.dark.linktre {
  margin-top: 8px;
  margin-bottom: 8px;
}

.div-block-17 {
  /* background-color: #0c3273; */
  font-family: var(--body-font);
  padding: 20px;
  display: block;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}


/* Utility classes */
.mt-1 { margin-top: 1rem; }
.mb-1 { margin-bottom: 1rem; }
.mb-2 { margin-bottom: 2rem; }

/* Mobile responsiveness */
@media (max-width: 768px) {
  .resume-container {
    padding: 10px;
  }

  .resume-header h1 {
    font-size: 2rem;
  }

  .section h2 {
    font-size: 1.3rem;
  }
}

/* Include this import at the top of your CSS file or in the <head> of your HTML file */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap');
